.gpt4__blog-container_article{
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
    width: 100%;
    height: 100%;
    transition: 0.5s;
}
.gpt4__blog-container_article:hover {
    transform: scale(1.04);
}
.gpt4__blog-container_article-image {
    width: 100%;
    height: 100%;
    background: var(--color-bg);
}
.gpt4__blog-container_article-image img {
    width: 100%;
    height: 100%;
}
.gpt4__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}
.gpt4__blog-container_article-content p {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 700;
    line-height: 35px;
    color: #fff;
}
.gpt4__blog-container_article-content h3 {
    font-family: var(--font-family);
    font-size: 25px;
    font-weight: 800;
    line-height: 30px;
    color: #fff;
    margin-bottom: 5rem;
    cursor: pointer;
    transition: 0.3s;
}
.gpt4__blog-container_article-content h3:hover {
    color: #FF4820;
}
.gpt4__blog-container_article-content p:last-child {
    cursor: pointer;
    transition: 0.5s;
}
.gpt4__blog-container_article-content p:last-child:hover {
    cursor: pointer;
    color: #FF4820;
}

@media screen and (max-width: 550px) {
    .gpt3__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
}