.gpt4__features{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.gpt4__features-heading{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    text-align: left;
    margin-right: 5rem;
}

.gpt4__features-heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;

}
.gpt4__features-heading p{
    color: #FF8A71;
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
    cursor: pointer;
    margin-top: 2rem;
    transition: 0.2s;
}
.gpt4__features-heading p:hover {
    color: #fff;
}
.gpt4__features-container {
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}
@media screen and (max-width: 990px) {
    .gpt4__features{
        flex-direction: column;
    }
    .gpt4__features-heading {
        margin: 0 0 2rem 0;
    }
}
@media screen and (max-width: 550px) {
    .gpt4__features-heading h1{
        font-size: 28px;
        line-height: 38px;
    }
}